@charset "UTF-8";


// Fonts


$headings-font-family:    'PT Sans', sans-serif;
$headings-font-size:      1.375rem;
$headings-font-weight:    700 !default;
$headings-color:          #333333;
$headings-line-height:    1.1 !default;


$font-family-sans-serif: 'Roboto', sans-serif;
$font-size-base:          0.8125rem;
$font-weight-base:        normal;


$subtitle-font-size:      0.9375rem;
$subtitle-font-weight:    500 !default;
$subtitle-color:          #333333;


$h1-font-size:            $headings-font-size;
$h2-font-size:            $subtitle-font-size;






// Colors
$blue:                     #009AAD;
$secondary:                #6c757d;
$theme-colors:             (primary: $blue, secondary: $secondary);
$navbar-light-color:       #000;
$breadcrumb-bg:            transparent;
$input-border-color:       #E4E4E4;


// Buttons
$button-width: 112px;
$button-height: 30px;
$btn-font-weight: $font-weight-medium;
$btn-hover: $blue;
$btn-focused: #00636F;
$button-secondary-border-color: #EDEDED;




// Input
$input-height:            30px;





// Spacing
$card-spacer-y:            1.25rem;
$headings-margin-bottom:   23px;
$paragraph-margin-bottom:  13px;
$navbar-padding-x:         23px;    
$breadcrumb-padding-x:     23px;






// Sidebar Drawer
$sidebar-bg-color: #313347;
$sidebar-border-right: 0.0625rem solid #f2f2f2;
$sidebar-compact-width: 3.125rem;
$topbar-height: 60px;
$sidebar-width: 13.3rem;
$space-25: 1.1rem;
$space-15: 0.9375rem;
$space-10: 0.7375rem;
$space-5: 0.5375rem;
$background-base: #f5f7fa;
$sidebar-font-size: inherit;
$zindex-2: 1040;    
$text-color: #fff;    
$icon-hover-fill: $blue;
$icon-width: 2.5rem;
$icon-default-fill: #b3b1b1;




















