body
{
    
    overflow: hidden;
}

.row
{
    
    margin-bottom: px2rem(22)
}


.simplebar-content-wrapper
{
    
  padding-right: px2rem(17);    
    
}

.btn:focus,.btn:active {
   outline: none !important;
   box-shadow: none  !important;
}


