table a 
{
    
    color: $body-color;
    
    
    
}



table th
{
    
        font-family: $font-family-sans-serif;
    font-weight: $subtitle-font-weight;
    color: $subtitle-color;
    font-size: $subtitle-font-size;
    padding-bottom: px2rem(31.5) !important;
    vertical-align: unset !important;
    
    
}


.table-row {
    cursor: pointer;
}



.icon.small svg, .illustration.small svg {
    width: 1.5625rem;
    height: 1.5625rem;
    cursor: pointer;
}



.icon.xsmall svg {
    width: 1rem;
    height: 1rem;
    cursor: pointer;
}



.filters .dropdown-item.active, .dropdown-item:active
{
    
    background-color: inherit;
    color: inherit;
}


.filters .dropdown-item:hover
{
    
    background-color: inherit;
    
}


.width-filter-search
{
    
    
    width: px2rem(130) !important;
}





.table-header-title {
    text-transform: capitalize !important;
    display: inline-block;
    width: auto !important;
}







.table td
{
    
    
    padding-top: px2rem(18.5);
    padding-bottom: px2rem(15.5);
}




.admin-view-section
{
    
    margin-left: px2rem(50);
    
}




.bootstrap-select .dropdown-toggle:focus, .bootstrap-select>select.mobile-device:focus+.dropdown-toggle
{
   outline:  px2rem(0) !important;
    
    
    
}




.filter-option-inner-inner
{
    
    
        height: 26px;
    padding: 0.25rem 0.25rem;
}


.dropdown.bootstrap-select.show-tick
{
    
        border: 1px solid #E4E4E4;
    border-radius: 5px;
    
}



.nav-pills .nav-link.active, .nav-pills .show > .nav-link
{
    
    color: $blue;
    background-color: transparent;
    border-bottom: 3px solid $blue;
    border-radius: 0;
    
}




.outer-wrapper
{
    
    height: calc(100vh - 135px);
    overflow-x: auto; 
    padding-bottom: px2rem(50);
    
}




