@charset "UTF-8";


.landing-portal h1.header-title
{
    
   font-size: px2rem(40);
    
}


.landing-portal p.subheading
{
    
    
    font-size: px2rem(25);
    
    
}



.plan-selected
{
    
    border: 0.0625rem solid $primary !important;
    background-color: #fdfdfd;
}

.plan-selected::after {
    content: url("/assets/img/checked.svg");
   
    right: 18px;
    position: absolute;
  
    top: 18px;
    color: $primary;
}