@charset "UTF-8";

.quick-invite
{
    
    
  .card
    {
        
        height: px2rem(300);
      
        
        
    }
    
    
    .emails-wrapper
    {
        
        height: 120px;
        margin-left: px2rem(20);
        margin-right: px2rem(3);
        margin-bottom: px2rem(20);
        
    }
    
    
    .emails-row
    {
        
        margin-bottom: px2rem(8);
    }
    
     
    .quick-invite-table
    {
        
        
         td
        {
            
            padding: 0.35rem;
        }
        
        td:nth-child(1)
        {
            
            width: 60%;
        }
         td:nth-child(2)
        {
            
            width: 30%;
        }
         td:nth-child(3)
        {
            
            width: 10%;
        }
        
       
        
        
    }
    
   
    
}



.dashboard-other-widgets
{
    
    .card
    {
        
        height: px2rem(300);
       
        
        
    }
    
     .card-body
    {
        
        min-height: 1px;
    padding: 1.25rem 1.25rem 0 1.25rem;
    }
    
    
    .emails-wrapper
    {
        
        height: px2rem(214);
        margin-left: px2rem(20);
        margin-right: px2rem(3);
        margin-bottom: px2rem(20);
        
    }
    
    
    .emails-row
    {
        
        margin-bottom: px2rem(8);
    }
    
    
    
    .other-table
    {
        
        td:nth-child(1)
        {
            
            width: 22%;
        }
        
        td:nth-child(2)
        {
            
            width: 33%;
        }
        
        td:nth-child(3)
        {
            
            width: 35%;
        }
        
        td:nth-child(4) 
        {
            
            width: 10%;
        }
        
        
        
        
        
       td
        {
            
            padding: 0.35rem;
        }
        
    }

    
    
    
    
    
    
    
}














